import React, { useEffect, useState } from 'react'
import PropTypes from 'prop-types'
import Helmet from 'react-helmet'
import { StaticQuery, graphql } from 'gatsby'
// import { oneLine, stripIndent } from "common-tags"

import Box from 'components/Box';
import theme from 'components/ThemeProvider/theme';

import Header from '../Header'

// const id = 'GTM-5GT6T9V'

// const GTM = oneLine(stripIndent`
// (function(w,d,s,l,i){w[l]=w[l]||[];w[l].push({'gtm.start':
// new Date().getTime(),event:'gtm.js'});var f=d.getElementsByTagName(s)[0],
// j=d.createElement(s),dl=l!='dataLayer'?'&l='+l:'';j.async=true;j.src=
// 'https://www.googletagmanager.com/gtm.js?id='+i+dl;f.parentNode.insertBefore(j,f);
// })(window,document,'script','dataLayer','${id}');`)

// const GTMIframe = `<iframe src="https://www.googletagmanager.com/ns.html?id=${id}" height="0" width="0" style="display: none; visibility: hidden" aria-hidden="true"></iframe>`

const Layout = ({ children, noHeader, onOpen, overBanner }) => {
  // const [loaded, setLoaded] = useState()
  // const [loaded, setLoaded] = useState(process.env.NODE_ENV === 'production')
  // useEffect(() => {
  //   setTimeout(() => setLoaded(true))
  // }, [])
  return (
    <StaticQuery
      query={graphql`
        query SiteTitleQuery {
          site {
            siteMetadata {
              title
              description
              url
            }
          }
        }
      `}
      render={({ site: { siteMetadata: { title, description, url } } }) => {
        return (
          <>
            {/* {loaded && (
              <noscript
                key="plugin-google-tagmanager"
                dangerouslySetInnerHTML={{ __html: GTMIframe }}
              />
            )} */}

            <Helmet>
              <html lang="zh-Hant-TW" />
              <title>{title}</title>
              <meta name="description" content={description} />
              <meta name="msapplication-TileColor" content="#da532c" />
              <meta name="theme-color" content="#ffffff" />
              <meta property="og:url" content={url} />
              <meta property="og:title" content={title} />
              <meta property="og:description" content={description} />
              <meta property="og:image" content={`${url}/fb.jpeg`} />
              {/* {loaded && <script type="text/javascript">{GTM}</script>} */}
            </Helmet>
            {!noHeader && <Header onOpen={onOpen} overBanner={overBanner} height={theme.headerHeight} />}
            <Box as="main" pt={!noHeader && theme.headerHeight} height="100%">
              {children}
            </Box>
          </>
        );
      }}
    />
  )
}

Layout.propTypes = {
  children: PropTypes.node.isRequired,
}

export default Layout
